@import url(https://fonts.googleapis.com/css?family=Roboto:100,100italic,300,300italic,400,400italic,500,500italic,700,700italic,900,900italic&subset=latin,latin-ext,cyrillic,cyrillic-ext,greek-ext,greek,vietnamese);
@import url(https://fonts.googleapis.com/css?family=Poppins:100,100italic,300,300italic,400,400italic,500,500italic,700,700italic,900,900italic&subset=latin,latin-ext,cyrillic,cyrillic-ext,greek-ext,greek,vietnamese);
@import url(https://fonts.googleapis.com/css?family=Raleway:100,100italic,300,300italic,400,400italic,500,500italic,700,700italic,900,900italic&subset=latin,latin-ext,cyrillic,cyrillic-ext,greek-ext,greek,vietnamese);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.content {
  font-family: "Poppins";
}

html {
  overflow-x: hidden;
}

.detection-button {
  position: fixed;
  width: 40px;
  height: 40px;
  border-radius: 50px;
  right: 30px;
  bottom: 30px;
  background-color: #BECF41;
  z-index: 99999;
}

.detection-button:hover { 
  background-color: #a7b830;
}

.home-play-button {
  color: #fff;
  font-size: 24px;
  position: absolute;
  top: 7px;
  left: 8px;
}

.header-bar {
    width: 100%;
    height: 390px;
    position: relative;
    overflow: hidden;
}

.header-bar::before {
  content: '';
  position: absolute;
  right: -90%;
  top: 20%;
  width: 250%;
  height: 300%;
  z-index: -1;
  background-color: #f4fce3;
  -webkit-transform: skewY(120deg);
          transform: skewY(120deg);
}

.header-text {
    float: left;
    width: 50%;
    margin-top: 150px;
    margin-left: 60px;
}

.header-image{
    float:right; 
    width: 600px;
    margin-right: -5%;
}

.header-title {
    font-size: 50pt;
    font-weight: 500;
    background: -webkit-linear-gradient(#F7D441, #A7CD40);
    -webkit-background-clip: text;
    background-clip:text;
    -webkit-text-fill-color: transparent;
  }
  
  .header-subtitle{
    font-size: 12pt;
    font-weight: 400;
    color: darkgray;
    letter-spacing: 1pt;
    margin-left: 30px;
    margin-top:-20px;
  }

  .start-btn-bar{
      width: 100%;
      margin-left: 150px;
      margin-top: 30px;
  }

  .start-btn {
    font-weight: 400;
    font-size: 20px;
    display: inline-block;
    padding: 8px 30px 12px 30px;
    letter-spacing: 1pt;
    border-radius: 3px;
    transition: 0.5s;
    color: #fff !important;
    background: #BECF41;
    position: relative;
  }

  .start-btn:hover {
    background: #a7b830;
  }

  /* INFO BAR */
.info-bar {
  margin-top: 6%;
  height: 380px;
}

.data-image {
  margin: 0% 5% 0% 5%;
  width: 35%;
}

#collaboration-pic {
  margin: 0% 10% 0% 5%;
  width: 30%;
}

.info-text {
  width: 47%;
}

.info-title{
  font-size: 24pt;
  font-weight: 500;
  color: #a7b830;
}

.info-description {
  font-size: 12pt;
  color: #77825f;
}

.left {
  float: left;
}

.right {
  float: right;
}

/* FREQUENTLY ASKED */

.frequently-asked{
  margin-top: 2%;
  height: auto;
  padding-bottom: 5%;
  background-color: #F4FCE3;
  text-align: center;
}

a {
  color: #AFB830;
  font-weight: 500;
}

a:hover {
  color: #BECF41;
  text-decoration: none;
}

.frequently-asked-title{
  color: #AFB830;
  font-weight: 400;
  font-size: 25pt;
  padding-top: 5%;
}

.frequently-asked-description{
  color: #77825f;
  font-size: 12pt;
  margin-bottom: 3%;
}

.frequently-asked-container {
  border: none;
  margin: auto;
  left: 0;
  right: 0;
  width: 60%;
  position: relative;
  display: inline-block;
  color: #77825f;
  padding: 0.8% 0%;
  margin-bottom: 0.8%;
  border-radius: 10px;
}

.frequently-asked-header:hover{
  color: #AFB830;
}

.frequently-asked-header {
  background-color: white;
  display: flow-root;
  border:none;
}

.q-icon {
  height: 20px;
}

.q-icon-container{
  margin: 1% 2%;
}

.question-text{
  margin: 1% 2%;
  font-size: 13pt;
  font-weight: 500;
}

.collapsing {
  width: 100%;
}

.frequently-asked-answer {
  text-align:left;
  margin-top: -2%;
  margin-left: 6.8%;
}
/* HEADER */
a {
  text-decoration: none;
}

  header {
    position: relative;
    margin: auto;
    max-width: 380px;
    padding-top: 8px;
  }
  
  header img {
    margin-top: 8px;
    float: left;
    width: 22px;
  }

  .loader {
    position: fixed;
    left: 50%;
    bottom: 50%;
  }
  
  .title {
    font-family: 'Roboto';
    font-size: 24px;
    font-weight: 1000;
    background: -webkit-linear-gradient(#F7D441, #A7CD40);
    -webkit-background-clip: text;
    background-clip:text;
    -webkit-text-fill-color: transparent;
    margin-left: 10.5%;
    margin-bottom: -1%;
  }
  
  .subtitle{
    font-size: 10px;
    font-weight: 400;
    color: darkgray;
    letter-spacing: 1pt;
    margin-left: 14%;
    margin-top: -2%;
  }

  .video {
      text-align: center;
      transition: 0.5s;
  }
    
  .vid-and-canvas {
      position: absolute;
      margin-left: auto;
      margin-right: auto;
      margin-top: auto;
      margin-bottom: auto;
      left: 0;
      right: 0;
      top: 10%;
      text-align: center;
  }

  #main-button-bar {
      position: fixed;
      margin: auto;
      left: 48%;
      bottom: 3%;
      text-align: center;
      position: absolute;
      display: flex;
      height: 97%;
      align-items: flex-end;
  }

  .main-btn-container {
    margin: 0% 40%;
  }

  .main-btn{
    color: gray;
  }

  .main-btn:hover {
    color: lightgray;
  }
